/* These colors and their names are based on the values from the design system */
/* See: https://github.com/Amsterdam/design-system/blob/main/proprietary/tokens/src/brand/amsterdam/color.tokens.json */
/* The dark yellow color is added on top of the design system colors */

:root {
	--report-color-primairy-black: #000;
	--report-color-primairy-white: #fff;
	--report-color-primairy-blue: #004699;
	--report-color-primairy-red: #ec0000;
	--report-color-dark-blue: #102e62;
	--report-color-orange: #ff9100;
	--report-color-yellow: #ffe600;
	--report-color-green: #bed200;
	--report-color-dark-green: #00a03c;
	--report-color-blue: #009dec;
	--report-color-purple: #a00078;
	--report-color-magenta: #e50082;
	--report-color-neutral-grey-1: #e6e6e6;
	--report-color-neutral-grey-2: #b4b4b4;
	--report-color-neutral-grey-3: #767676;
	--report-color-neutral-grey-4: #323232;
	--report-color-dark-yellow: #ffc000;
}
